<template>
	<div id="purse">
		<van-nav-bar>
			<div slot="title">
				收入提取
			</div>
			<div @click="$router.push({path:'/record'})" slot="title" style="position: fixed;top:0.01rem;right: 1rem;font-size: 15px;">
				提现记录
			</div>
			<van-icon name="arrow-left" slot="left" color="black" @click="$router.go(-1)"/>
		</van-nav-bar>

		<img class="myimg" :src="headimg" alt="">
		<div style="font-size: 18px;">账号余额</div>
		<div class="money">&yen;{{balance}}</div>

		<div class="user" style="padding-top: 40px">
			<div class="help-input">
			  <img src="../../static/img/icon_cz@3x.png"/>
			  <input type="text" placeholder="请输入提现金额" v-model="money">
			</div>
		</div>
		<div class="user">
			<div class="help-input">
			  <img src="../../static/img/man.png"/>
			  <input type="number" placeholder="请输入支付宝账号" v-model="phone">
			</div>
		</div>
		<div class="sub" @click="purse">确定</div>

	</div>
</template>

<script>
    export default {
      name: "purse",
      data(){
				return{
					headimg:'',
					balance:'',
					money:'',
					phone:'',
				}
      },
			created (){
				this.mymessage()
			},
      methods:{
				mymessage() {
					this.$POST('/user/user-info').then(res =>{
						console.log(res);
						if (res.code === 200) {
						this.headimg = res.data.avatar
							this.balance = res.data.credit4
						} else {
							this.$toast(res.msg)
						}
					}).catch(res => {
						this.$toast('连接服务器失败!');
						console.log(res);
					})
				},

				purse(){
					var regx = /^(([1-9]\d{0,9})|0)(\.\d{1,2})?$/;
						if(this.money==''){
							this.$toast('金额不能为空');
						return false;
					}
					if(!regx.test(this.money)){
						this.$toast('请正确输入提现金额');
						return false;
					}
					var reg=11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
					if(this.phone==''){
						this.$toast('账号不能为空');
						return false;
					}
					if(!reg.test(this.phone)){
						this.$toast('账号格式不正确');
						return false;
					}
					let params = {
						withdraw: this.money,
						account: this.phone,
					}
					this.$POST('/user/withdrawl',params,true).then(res =>{
						console.log(res);
						if(res.code===200 ){
							this.$toast(res.msg);
						}else {
							this.$toast(res.msg);
						}
					}).catch(res=>{
						this.$toast('连接服务器失败!');
						console.log(res);
					})
				},
      }
    }

</script>

<style scoped>
	.myimg{width: 75px;height: 75px;margin-top: 20px;margin-bottom: 8px;border-radius: 50%;}
	.money{padding: 5px 0;font-size: 20px;}

	.user{
	    width: 80%;
	    margin: 0 auto;
	    position: relative;
	    top: -50px;
	  }
	.help-input{
	  text-align: left;
	  margin-top: 20px;
	  border-bottom: 1px solid #bfbfbf;
	  padding-bottom: 10px;
	  margin-bottom:25px;
	}
	 .help-input>img{
	   display: inline-block;
	   width: 25px;
	   position: relative;
	   top:8px;
	   margin-left: 5px;
	 }
	.help-input>input{
	  width:60%;
	  border: 0;
	  font-size: 16px;
	  margin-left: 5px;
	}
	 .sub{
	   width: 80%;
	   margin: 0 auto;
	   border-radius: 30px;
	   background: linear-gradient(to right, #5AB4FF , #5AB4FF,#5AB4FF);
	   height: 45px;
	   line-height: 45px;
	   color: #ffffff;
	   font-size: 18px;
	   margin-top: -15px;
	 }
	 .dsf{
	   width: 80%;
	   margin: 0 auto;
	   border-top: 1px solid #bfbfbf;
	   margin-top: 50px;
	 }
	.dsf>span{
	  color: #959595;
	  position: relative;
	  top: -12px;
	  display: block;
	  background: #ffffff;
	  width:100px;
	  margin: 0 auto;
	}
	.dsf img{
	  display: inline-block;
	  width:55px;
	  height:55px;
	  border-radius:55px;
	}
	.dsf>div{
	  width:40%;
	  margin: 0 auto;
	}
	.dsf>div>img:first-child{
	  float: left;
	}
	.dsf>div>img:last-child{
	  float: right;
	}
	.help-input>span{
	  float: right;
	  line-height: 50px;
	  color: #fe513f;
	}
</style>



